import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import util from 'preact-util';

import wutil from '../../lib/wifet-util';
import LineGraph from '../../components/lineGraph';
import Map from '../../components/map';

const initialState = {};

@observer
class Production extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { windmillStore, weatherYrStore, productionStore, priceStore } = this.props.stores;
		const { currentWindparkName, windparkCapacity } = windmillStore;

		const xMin = wutil.getDateDiffTime(-4);
		const xMax = wutil.getDateDiffTime(10);
		const now = Math.floor(new Date().getTime() / 1000);
		const markerDays = wutil.timeRangeDays(-4, 11);
		this.setState({ now, xMin, xMax, markerDays });

		await this.loadYrData();
		await this.loadOpenData();
		const forcastYrData = weatherYrStore.getWindSpeeds(currentWindparkName);
		priceStore.generateData();

		const { prices } = priceStore;
		productionStore.generateData(forcastYrData, prices, windparkCapacity);
	}

	async loadYrData() {
		const { weatherYrStore, windmillStore } = this.props.stores;
		const { currentWindpark } = windmillStore;

		weatherYrStore.updateQueryFilter({
			name: currentWindpark.name,
			altitude: currentWindpark.position.altitude,
			lat: currentWindpark.position.lat,
			lon: currentWindpark.position.lon,
		});
		return await weatherYrStore.load();
	}

	async loadOpenData() {
		const { weatherOpenStore, windmillStore } = this.props.stores;
		const { currentWindpark } = windmillStore;

		weatherOpenStore.updateQueryFilter({
			name: currentWindpark.name,
			lat: currentWindpark.position.lat,
			lon: currentWindpark.position.lon,
		});
		return await weatherOpenStore.load();
	}

	setWindpark = (e) => {
		const { windmillStore } = this.props.stores;
		const windpark = e.target.dataset.windpark;
		windmillStore.setCurrentWindparkName(windpark);
		this.loadAll();
	}

	componentDidMount() {
		this.loadAll();
	}

	render() {
		const { now, xMin, xMax, markerDays } = this.state;
		const { appState, windmillStore, productionStore, weatherYrStore, weatherOpenStore } = this.props.stores;
        const { darkmode } = appState;
		const { windparks, currentWindparkName, currentWindpark, windparkCapacity, windparkMaxCapacity } = windmillStore;
		const windparkCapacityPercent = windparkCapacity / windparkMaxCapacity * 100
		const { productionData, productionBoostDataFiltered: productionBoostData, productionLongLifeDataFiltered: productionLongLifeData } = productionStore;

		const forcastYrData = weatherYrStore.getWindSpeeds(currentWindparkName);
		const windspeedsOpen = weatherOpenStore.getWindSpeeds(currentWindparkName);

		return (
			<div class='container-fluid mt-5 pt-2' style='margin-bottom: 200px;'>
				<div class='row'>
					<div class='col-5'>
						<h1><i class='fas fa-chart-bar' /> Production - {currentWindparkName}</h1>
						<span class='text-muted'>Capacity at</span> {util.format(windparkCapacityPercent, 1)}%
						<span class='text-muted'>, {windparkCapacity}/{windparkMaxCapacity} Mw</span>
					</div>
					<div class='col-7 pt-2'>
						<ul class='nav nav-pills float-right'>
						{windparks && windparks.map(wp => (
							<li class='nav-item'>
								<button
									class={`btn btn-link nav-link ${wp.name === currentWindparkName ? 'active' : ''}`}
									onClick={this.setWindpark}
									data-windpark={wp.name}
								>{wp.name}</button>
							</li>
						))}
						</ul>
					</div>
					<div class='col-12'>
						<LineGraph
							stores={this.props.stores}
							width={1200}
							height={400}
							showXTicks={1}
							xTicksVal={val => util.isoDate(val)}
							xMin={xMin}
							xMax={xMax}
							paddingTop={30}

							dataLeft={[productionData, productionBoostData, productionLongLifeData]}
							legendLeft={['Normal production', 'Boost 10%', 'Long life -5%']}
							yMaxLeft={windparkCapacity}
							yMinLeft={0}
							showYTicksLeft={1}
							// yTicks={['500Mw', '400Mw', '300Mw', '200Mw', '100Mw', '0Mw']}
							yTicksLeftPostfix={'Mw'}
							unitLeft={['Mw', 'Mw', 'Mw']}
							avgWindowLeft={[1, 1, 1]}
							smoothLeft={[1, 1, 1]}

							// dataRight={[forcastYrData, windspeedsOpen]}
							// legendRight={['Wind forcast Yr', 'Wind OpenWeather']}
							// yMaxRight={30}
							// yMinRight={0}
							// showYTicksRight={1}
							// yTicksRightPostfix='m/s'
							// unitRight={['m/s', 'm/s']}
							// smoothRight={[1, 1]}

							marker={now}
							markers={markerDays}
						/>
					</div>
				</div>
				<div class='row pt-4'>
					<div class='col-12' style='height: 50vh;'>
						<Map stores={this.props.stores}
							darkmode={darkmode}
							center={[currentWindpark.position.lat, currentWindpark.position.lon]}
							zoom={currentWindpark.zoom}
							waypoints={currentWindpark.windmills}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Production;
